import React, {Component} from 'react';
import './residencias.css'
import Layout from '../components/layout'

class Investigacion extends Component{
    render(){
        return(
            <Layout>
                <div className="container">
                    <header className="major">
                        <h2 >Aromatorio (UNTreF)</h2>
                    </header>
                    <p>Durante 2016 y 2017 fui Becaria de Investigación, bajo la dirección de <a target="blank" href="http://arteyciencia.untref.edu.ar/bruno-mesz">Bruno Mesz</a> y la coordinación de <a target="blank" href="http://sebastiantedesco.com/en/works/">Sebastián Tedesco</a>. </p>
                    <p><a target="blank" href="http://sebastiantedesco.com/wp-content/uploads/Resumen-Desarrollos-Aromatorio-low.pdf">Aromatorio</a> es un proyecto avanza en el análisis de las transmodalidades entre la música, la emoción y los aromas, basado en experimentos que estudian correspondencias entre olfato, gusto y audición.</p>
                    <p>En una primera etapa, colaboré con investigaciones de correspondencia entre textura visual y timbre. Luego investigué la creación de poemas aromáticos, valiéndome de herramientas de procesamiento de 
                     lenguaje natural y análisis sentimental de textos. </p>
                    <p>Aporté en el desarrollo de una mesa aromática (dispositivo con 16 válvulas que permiten expeler aromas de modo invididual o en combinación, con control de intensidad) y en el de <a target="blank" href="https://brainwine.org/instalaciones/">copas transmodales</a> (que sensan la inclinación al oler/sorber y controlan sonido). Presencié el desarrollo de un vaso sonoro (que permite oir diferentes tipos de composiciones o eventos sonoros a través de conducción ósea) y colaboré con experimentos que <a target="blank" href="http://www.qianjanicewang.com/">Janice Wang</a> realizó en las instalaciones del Laboratorio donde trabajamos.</p>
                   
                    <p>Esta fue una experiencia muy enriquecedora para mí, no sólo por el caudal de conocimiento que absorí sino por la calidad humana y la dinámica colaborativa entre les integrantes del proyecto.</p>
                </div>
            </Layout>
        );
    }
}

export default Investigacion